@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Work+Sans:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
}
.App {
    min-height: 100vh ;
    display: flex ;
    background-image: url("../Images/background.jpg");
    background-size: cover;
    background-position: center;
}

::-webkit-scrollbar {
    width: 0px;
}

::-webkit-scrollbar-thumb {
    background: rgba(136 , 136 , 136 , 0.281);
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}